}
.modal {
	width: 90%;
	background: white;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	position: fixed;
	top: 20vh;
	left: 5%;
}

.modal__header {
	padding: 1rem;
	background: #01d1d1;
}

.modal__header h1 {
	margin: 0;
	font-size: 1.25rem;
}

.modal__content {
	padding: 1rem;
}

.modal__actions {
	display: flex;
	justify-content: flex-end;
	padding: 1rem;
}

@media(min-width: 768px) {
	.modal {
		width: 30rem;
		left: calc((100% - 30rem) / 2)
	}
}
.backdrop {
	-webkit-animation: fadein 1s;
	        animation: fadein 1s;
	-webkit-transition: all ease-in;
	transition: all ease-in;
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100%;
	background: rgba(0, 0, 0, 0.75);
}

@-webkit-keyframes fadin {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}

@keyframes fadin {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}
